"use client";

import { useState, useEffect } from 'react';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { format, parse, isWithinInterval } from 'date-fns';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogFooter,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { toast } from "sonner";
import { Alert, AlertDescription } from "@/components/ui/alert";

type BrandAttendee = {
  name: string;
  designation: string;
};

type Attendee = {
  name: string;
  optional: boolean;
};

type EventType = {
  id: string;
  title: string;
  date: string;
  startTime: string;
  endTime: string;
  description?: string;
  category?: string;
  location?: string;
  attendees?: Attendee[];
  status: 'confirmed' | 'tentative';
  schedulerName: string;
  brandAttendees: BrandAttendee[];
};

const categoryOptions = ["Liquor", "Tobacco", "PNC", "Confectionary", "Fashion"];

const attendeesOptions = {
  Liquor: ["Varun Khanna", "Vishwanath Iyer", "Chiragh Oberoi", "Ashish Chopra"],
  Tobacco: ["Varun Khanna", "Vishwanath Iyer", "Chiragh Oberoi", "Ashish Chopra"],
  PNC: ["Payal Lal", "Vishwanath Iyer", "Chiragh Oberoi", "Ashish Chopra"],
  Confectionary: ["Payal Lal", "Vishwanath Iyer", "Chiragh Oberoi", "Ashish Chopra"],
  Fashion: ["Payal Lal", "Vishwanath Iyer", "Chiragh Oberoi", "Ashish Chopra"],
};

const categoryColors = {
  Liquor: '#A52A2A',
  Tobacco: '#D2691E',
  Confectionary: '#FFD700',
  PNC: '#CD5C5C',
  Fashion: '#3CB371',
};

export default function CalendarScheduler() {
  const [events, setEvents] = useState<EventType[]>([]);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [conflictMessage, setConflictMessage] = useState<string | null>(null);
  const [editingEventId, setEditingEventId] = useState<string | null>(null);
  const [newEvent, setNewEvent] = useState<Partial<EventType>>({
    title: '',
    date: format(new Date(), 'yyyy-MM-dd'),
    startTime: '09:00',
    endTime: '10:00',
    description: '',
    category: '',
    location: '',
    attendees: [],
    status: 'confirmed',
    schedulerName: '',
    brandAttendees: [],
  });
  
  const fetchEvents = async () => {
    setLoading(true);
    setError(null);
    try {
      const res = await fetch('/api/calendar-events');
      if (!res.ok) throw new Error('Failed to load events.');
      const data = await res.json();
      setEvents(data.events);
    } catch (err: any) {
      setError(err.message);
      toast.error("Failed to load events");
    } finally {
      setLoading(false);
    }
  };

  const checkForConflicts = () => {
    if (!newEvent.date || !newEvent.startTime || !newEvent.endTime || !newEvent.attendees) return null;

    const newStartTime = parse(newEvent.startTime, 'HH:mm', new Date());
    const newEndTime = parse(newEvent.endTime, 'HH:mm', new Date());

    const conflictingEvents = events.filter(event => {
      if (event.date !== newEvent.date) return false;

      const eventStartTime = parse(event.startTime, 'HH:mm', new Date());
      const eventEndTime = parse(event.endTime, 'HH:mm', new Date());

      const isTimeOverlap = (
        isWithinInterval(newStartTime, { start: eventStartTime, end: eventEndTime }) ||
        isWithinInterval(newEndTime, { start: eventStartTime, end: eventEndTime }) ||
        isWithinInterval(eventStartTime, { start: newStartTime, end: newEndTime })
      );

      if (!isTimeOverlap) return false;

      // Check for common mandatory attendees
      const commonMandatoryAttendees = event.attendees?.filter(eventAttendee => 
        !eventAttendee.optional && 
        newEvent.attendees?.some(newAttendee => 
          !newAttendee.optional && newAttendee.name === eventAttendee.name
        )
      );

      return commonMandatoryAttendees && commonMandatoryAttendees.length > 0 
        ? commonMandatoryAttendees 
        : false;
    });

    if (conflictingEvents.length > 0) {
      const conflictingAttendees = new Set();
      conflictingEvents.forEach(event => {
        event.attendees?.forEach(attendee => {
          if (!attendee.optional && 
              newEvent.attendees?.some(newAttendee => 
                !newAttendee.optional && newAttendee.name === attendee.name
              )) {
            conflictingAttendees.add(attendee.name);
          }
        });
      });

      if (conflictingAttendees.size > 0) {
        return `Cannot schedule meeting. The following mandatory attendees have conflicts: ${Array.from(conflictingAttendees).join(', ')}`;
      }
    }

    return null;
  };

  const handleCreateEvent = async () => {
    if (!newEvent.title || !newEvent.date || !newEvent.startTime || !newEvent.endTime || 
        !newEvent.category || !newEvent.location || !newEvent.schedulerName) {
      toast.error("Please fill in all required fields");
      return;
    }

    const conflict = checkForConflicts();
    if (conflict) {
      setConflictMessage(conflict);
      return;
    }

    try {
      const method = editingEventId ? 'PUT' : 'POST';
      const url = editingEventId ? `/api/calendar-events/${editingEventId}` : '/api/calendar-events';

      const res = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(newEvent),
      });

      if (!res.ok) throw new Error('Failed to save event.');
      
      await fetchEvents();
      setIsDialogOpen(false);
      setEditingEventId(null);
      setNewEvent({
        title: '',
        date: format(new Date(), 'yyyy-MM-dd'),
        startTime: '09:00',
        endTime: '10:00',
        description: '',
        category: '',
        location: '',
        attendees: [],
        status: 'confirmed',
        schedulerName: '',
        brandAttendees: [],
      });
      setConflictMessage(null);
      toast.success(editingEventId ? "Event updated successfully" : "Event created successfully");
    } catch (err: any) {
      toast.error("Failed to save event");
      setError(err.message);
    }
  };
  
  useEffect(() => {
    fetchEvents();
  }, []);

  const filteredEvents = events.filter(event => 
    event.date === format(selectedDate || new Date(), 'yyyy-MM-dd')
  );

  const handleCategoryChange = (category: string) => {
    setNewEvent(prev => ({
      ...prev,
      category,
      attendees: [],
    }));
  };

  const handleAttendeeToggle = (name: string) => {
    if (!newEvent.attendees) return;
    const existing = newEvent.attendees.find(a => a.name === name);
    if (existing) {
      setNewEvent(prev => ({
        ...prev,
        attendees: prev.attendees?.filter(a => a.name !== name),
      }));
    } else {
      setNewEvent(prev => ({
        ...prev,
        attendees: [...(prev.attendees || []), { name, optional: false }],
      }));
    }
  };

  const toggleAttendeeOptional = (name: string) => {
    if (!newEvent.attendees) return;
    setNewEvent(prev => ({
      ...prev,
      attendees: prev.attendees?.map(a => a.name === name ? { ...a, optional: !a.optional } : a),
    }));
  };

  const getAttendeesForCategory = () => {
    if (!newEvent.category) return [];
    return attendeesOptions[newEvent.category as keyof typeof attendeesOptions] || [];
  };

  const addBrandAttendee = () => {
    setNewEvent(prev => ({
      ...prev,
      brandAttendees: [
        ...(prev.brandAttendees || []),
        { name: '', designation: '' }
      ],
    }));
  };

  const updateBrandAttendee = (index: number, field: keyof BrandAttendee, value: string) => {
    setNewEvent(prev => ({
      ...prev,
      brandAttendees: prev.brandAttendees?.map((attendee, i) => 
        i === index ? { ...attendee, [field]: value } : attendee
      ) || [],
    }));
  };

  const removeBrandAttendee = (index: number) => {
    setNewEvent(prev => ({
      ...prev,
      brandAttendees: prev.brandAttendees?.filter((_, i) => i !== index) || [],
    }));
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-semibold">Schedule</h2>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button variant="default">
              Create Event
            </Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[600px]">
            <DialogHeader>
              <DialogTitle>Create New Event</DialogTitle>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid gap-2">
                <Label htmlFor="title">Title *</Label>
                <Input
                  id="title"
                  value={newEvent.title}
                  onChange={(e) => setNewEvent(prev => ({ ...prev, title: e.target.value }))}
                  placeholder="Event title"
                  className="col-span-3"
                />
              </div>
              <div className="grid gap-2">
                <Label htmlFor="schedulerName">Scheduler Name *</Label>
                <Input
                  id="schedulerName"
                  value={newEvent.schedulerName}
                  onChange={(e) => setNewEvent(prev => ({ ...prev, schedulerName: e.target.value }))}
                  placeholder="Your name"
                  className="col-span-3"
                />
              </div>
              <div className="grid gap-2">
                <Label htmlFor="date">Date *</Label>
                <Input
                  id="date"
                  type="date"
                  value={newEvent.date}
                  onChange={(e) => setNewEvent(prev => ({ ...prev, date: e.target.value }))}
                  className="col-span-3"
                />
              </div>
              <div className="grid grid-cols-2 gap-4">
                <div className="grid gap-2">
                  <Label htmlFor="startTime">Start Time *</Label>
                  <Input
                    id="startTime"
                    type="time"
                    value={newEvent.startTime}
                    onChange={(e) => setNewEvent(prev => ({ ...prev, startTime: e.target.value }))}
                  />
                </div>
                <div className="grid gap-2">
                  <Label htmlFor="endTime">End Time *</Label>
                  <Input
                    id="endTime"
                    type="time"
                    value={newEvent.endTime}
                    onChange={(e) => setNewEvent(prev => ({ ...prev, endTime: e.target.value }))}
                  />
                </div>
              </div>
              <div className="grid gap-2">
                <Label htmlFor="status">Status *</Label>
                <Select
                  value={newEvent.status}
                  onValueChange={(value) => setNewEvent(prev => ({ ...prev, status: value as 'confirmed' | 'tentative' }))}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="confirmed">Confirmed</SelectItem>
                    <SelectItem value="tentative">Tentative</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid gap-2">
                <Label htmlFor="category">Category *</Label>
                <Select
                  onValueChange={handleCategoryChange}
                  value={newEvent.category || ''}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Select a category" />
                  </SelectTrigger>
                  <SelectContent>
                    {categoryOptions.map((cat) => (
                      <SelectItem key={cat} value={cat}>{cat}</SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="grid gap-2">
                <Label htmlFor="location">Location *</Label>
                <Input
                  id="location"
                  value={newEvent.location}
                  onChange={(e) => setNewEvent(prev => ({ ...prev, location: e.target.value }))}
                  placeholder="Location"
                  className="col-span-3"
                />
              </div>
              <div className="grid gap-2">
                <Label>DDFS Attendees</Label>
                <div className="space-y-2 max-h-40 overflow-y-auto border rounded p-2">
                  {getAttendeesForCategory().map((attendee) => {
                    const isChecked = newEvent.attendees?.some(a => a.name === attendee);
                    const optional = newEvent.attendees?.find(a => a.name === attendee)?.optional || false;
                    return (
                      <div key={attendee} className="flex items-center space-x-2">
                        <Checkbox
                          id={`attendee-${attendee}`}
                          checked={isChecked}
                          onCheckedChange={(checked: boolean) => {
                            if (checked) {
                              // Add as mandatory by default
                              setNewEvent(prev => ({
                                ...prev,
                                attendees: [...(prev.attendees || []), { name: attendee, optional: false }],
                              }));
                            } else {
                              // Remove attendee
                              setNewEvent(prev => ({
                                ...prev,
                                attendees: prev.attendees?.filter(a => a.name !== attendee),
                              }));
                            }
                          }}
                        />
                        <label htmlFor={`attendee-${attendee}`} className="flex-1">{attendee}</label>
                        {isChecked && (
                          <div className="flex items-center space-x-2">
                            <div className="flex items-center space-x-1">
                              <Checkbox
                                id={`mandatory-${attendee}`}
                                checked={!optional}
                                onCheckedChange={(checked: boolean) => {
                                  setNewEvent(prev => ({
                                    ...prev,
                                    attendees: prev.attendees?.map(a => a.name === attendee ? { ...a, optional: !checked } : a),
                                  }));
                                }}
                              />
                              <label htmlFor={`mandatory-${attendee}`}>Mandatory</label>
                            </div>
                            <div className="flex items-center space-x-1">
                              <Checkbox
                                id={`optional-${attendee}`}
                                checked={optional}
                                onCheckedChange={(checked: boolean) => {
                                  setNewEvent(prev => ({
                                    ...prev,
                                    attendees: prev.attendees?.map(a => a.name === attendee ? { ...a, optional: checked } : a),
                                  }));
                                }}
                              />
                              <label htmlFor={`optional-${attendee}`}>Optional</label>
                            </div>
                          </div>
                        )}
                      </div>
                    );
                  })}
                </div>
              </div>
              <div className="grid gap-2">
                <div className="flex justify-between items-center">
                  <Label>Brand Attendees</Label>
                  <Button type="button" variant="outline" onClick={addBrandAttendee}>
                    Add Brand Attendee
                  </Button>
                </div>
                <div className="space-y-4">
                  {newEvent.brandAttendees?.map((attendee, index) => (
                    <div key={index} className="grid grid-cols-[1fr,1fr,auto] gap-2 items-start">
                      <div>
                        <Input
                          placeholder="Name"
                          value={attendee.name}
                          onChange={(e) => updateBrandAttendee(index, 'name', e.target.value)}
                        />
                      </div>
                      <div>
                        <Input
                          placeholder="Designation"
                          value={attendee.designation}
                          onChange={(e) => updateBrandAttendee(index, 'designation', e.target.value)}
                        />
                      </div>
                      <Button
                        type="button"
                        variant="destructive"
                        onClick={() => removeBrandAttendee(index)}
                      >
                        Remove
                      </Button>
                    </div>
                  ))}
                </div>
              </div>
              <div className="grid gap-2">
                <Label htmlFor="description">Description</Label>
                <Textarea
                  id="description"
                  value={newEvent.description}
                  onChange={(e) => setNewEvent(prev => ({ ...prev, description: e.target.value }))}
                  placeholder="Event description"
                  className="col-span-3"
                />
              </div>
              {conflictMessage && (
                <Alert variant="destructive">
                  <AlertDescription>{conflictMessage}</AlertDescription>
                </Alert>
              )}
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
                Cancel
              </Button>
              <Button onClick={handleCreateEvent}>
                Create Event
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>

      {loading && (
        <div className="flex items-center justify-center h-32">
          <div className="text-gray-500">Loading events...</div>
        </div>
      )}
      
      {error && (
        <div className="bg-red-50 text-red-500 p-4 rounded-lg">
          Error: {error}
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <Card className="p-6">
          <Calendar
            mode="single"
            selected={selectedDate}
            onSelect={setSelectedDate}
            className="rounded-md border"
          />
        </Card>

        <Card className="p-6">
          <h3 className="text-lg font-semibold mb-4">
            Events for {format(selectedDate || new Date(), 'MMMM d, yyyy')}
          </h3>
      <div className="space-y-4">
        {filteredEvents.length === 0 ? (
          <p className="text-gray-500">No events scheduled for this day.</p>
        ) : (
          filteredEvents.map((event) => (
            <div 
              key={event.id} 
              className="p-4 rounded-lg border hover:border-gray-400 transition-colors cursor-pointer"
              style={{ 
                backgroundColor: event.category ? categoryColors[event.category as keyof typeof categoryColors] : 'white',
                color: event.status === 'tentative' ? 'red' : 'inherit'
              }}
              onDoubleClick={() => {
                setEditingEventId(event.id);
                setNewEvent(event);
                setIsDialogOpen(true);
              }}
            >
              <h4 className="font-semibold">{event.title}</h4>
              <p className="text-sm mt-1">
                {event.startTime} - {event.endTime}
              </p>
              <p className="text-sm mt-1">
                Status: {event.status}
              </p>
              <p className="text-sm mt-1">
                Location: {event.location}
              </p>
              <p className="text-sm mt-1">
                Scheduler: {event.schedulerName}
              </p>
              {event.description && (
                <div className="text-sm mt-2">
                  {event.description}
                </div>
              )}
              {event.attendees && event.attendees.length > 0 && (
                <div className="text-sm mt-2">
                  <strong>DDFS Attendees:</strong>
                  <ul className="list-disc list-inside">
                    {event.attendees.map((attendee, index) => (
                      <li key={index}>
                        {attendee.name} ({attendee.optional ? 'Optional' : 'Mandatory'})
                      </li>
                    ))}
                  </ul>
                </div>
              )}
              {event.brandAttendees && event.brandAttendees.length > 0 && (
                <div className="text-sm mt-2">
                  <strong>Brand Attendees:</strong>
                  <ul className="list-disc list-inside">
                    {event.brandAttendees.map((attendee, index) => (
                      <li key={index}>
                        {attendee.name} - {attendee.designation}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          ))
        )}
      </div>
        </Card>
      </div>
    </div>
  );
}
